.home {
  @media screen and (orientation: landscape) and (min-height: 500px) {
    .Header--inner {
      transform: rotate(-90deg);
      transform-origin: top left;
      min-width: 100vw;
    }
  }

  h2 {
    .location {
      display: block;
    }
  }

  header {
    padding: $padding-x;
    display: flex;
    align-items: flex-end;

    @media screen and (orientation: landscape) and (min-height: 500px) {
      width: 25vw;
      height: 100vh;
      min-height: 100vh;
      position: fixed;
      padding: 0 $padding-x-lg;
      z-index: 2;
    }

    @media screen and (orientation: portrait) {
      height: calc(50vh + #{$navHeight});
      min-height: calc(50vh + #{$navHeight});

      h1 {
        margin-top: $navHeight / 2;
      }
    }
  }

  main {
    display: flex;

    @media screen and (orientation: landscape) {
      flex-direction: row;
    }

    @media screen and (orientation: portrait) {
      flex-direction: column;
    }
  }

  #work {
    display: flex;
    flex-direction: column;
    border-radius: $pageRadius;
    overflow: hidden;

    @media screen and (orientation: landscape) and (min-height: 500px) {
      width: calc(75vw - 40px);
      // height: calc(100vh - 40px);
      margin-left: 25vw;
      // overflow-x: hidden;
      // overflow-y: auto;
      // scrollbar-width: thin;

      // /* width */
      // &::-webkit-scrollbar {
      //   width: 9px;
      // }

      // /* Track */
      // &::-webkit-scrollbar-track {
      //   background: transparent;
      // }

      // /* Handle */
      // &::-webkit-scrollbar-thumb {
      //   background: rgba(black, 0.1);
      //   width: 5px;
      //   border-radius: 10px;
      //   border: 2px solid white;

      //   &:hover {
      //     background: rgba(black, 0.2);
      //   }
      // }
    }

    @media screen and (orientation: landscape) {
      h5 {
        display: none;
      }
    }

    ul {
      margin-top: -1px;
    }
  }

  footer {
    font-family: $font-secondary;
    @media screen and (orientation: landscape) {
      position: fixed;
      bottom: $page-padding;
    }
    @media screen and (min-width: $breakpoint-sm) {
      bottom: $page-padding-lg;
    }
  }
}
